import { Col, Row, Slider, Typography } from 'antd';
import React from 'react'
import { chaikin, drawDots, drawLines, makeSplineFromPoint, splineToPoints, standarDeviationPoints } from '../../utils';
import { Range, useRange } from './useRange';
import { simplify } from '../../utils/simplify';
var seedrandom = require('seedrandom');

type Props = {
    size: {
        x: number;
        y: number;
    }
    lines: Point[][],
    lineCount: number;
    lineSplineSteps: number;
    smooth: number;
    simplfy: number;
}


const applyDevTopoints = (rng: any) => (points: Point[], dev: Point, cof = 0.2): Point[] => {
    const devSx = dev.x / 2;
    const devSy = dev.y / 2;
    const nPoints = [...points].map(point => ({
        x: point.x + (rng() * dev.x - devSx) * cof,
        y: point.y + (rng() * dev.y - devSy) * cof,
    }));
    // nPoints[0] = {
    //     x: points[0].x + (rng() * dev.x - devSx) * 0.01,
    //     y: points[0].y + (rng() * dev.y - devSy) * 0.01,
    // }
    // nPoints[nPoints.length - 1] = {
    //     x: points[nPoints.length - 1].x + (rng() * dev.x - devSx) * 0.01,
    //     y: points[nPoints.length - 1].y + (rng() * dev.y - devSy) * 0.02,
    // }

    return nPoints
}


const LineCanvas = ({
    size,
    lines,
    lineCount,
    lineSplineSteps,
    simplfy,
    smooth,
}: Props) => {

    const canvasRef = React.useRef<HTMLCanvasElement>(null);
    var rng = seedrandom('hello.');
    const devPntFn = applyDevTopoints(rng);

    const drawLine = (ctx: CanvasRenderingContext2D, points: Point[]) => {
        const dev = standarDeviationPoints(points);
        const devPoints = devPntFn(points, { x: 100, y: 100 }, 0.2);
        const spline = makeSplineFromPoint(devPoints);
        const splinePoints = splineToPoints(spline, lineSplineSteps);

        ctx.strokeStyle = 'rgb(153, 204, 255)';
        ctx.lineWidth = 3;
        const moved = devPntFn(splinePoints, { x: 50, y: 50 })
        const s = chaikin(moved, smooth)
        if (smooth > 0) {
            s.splice(-2 * smooth * smooth);
        }
        const xx = simplify(s, simplfy / 100, true );
        drawLines(ctx, xx);
    };

    React.useEffect(() => {
        if (canvasRef.current) {
            const canvas = canvasRef.current;
            const ctx = canvas.getContext('2d');
            if (ctx) {
                ctx.imageSmoothingEnabled = true;
                ctx.globalCompositeOperation = "multiply";
                ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
                lines.forEach(line => {
                    for (let i = 0; i < lineCount; i++) {
                        drawLine(ctx, line);
                    }
                })

            }

        }
    }, [size,
        lines,
        lineCount,
        lineSplineSteps,
        simplfy,
        smooth,]);

    return (
        <>
            <canvas width={size.x} height={size.y} ref={canvasRef} />


        </>
    )
}

export default LineCanvas;