import { Row, Col } from 'antd'
import React from 'react'
import LineCanvas from '../components/LineCanvas/LineCanvas'
import { Range, useRange } from '../components/LineCanvas/useRange'

type Props = {}

const points: Point[][] = [[
    { x: 100, y: 350 },
    { x: 200, y: 300 },
    { x: 300, y: 380 },
    { x: 400, y: 420 },
    { x: 500, y: 220 },
    { x: 600, y: 115 },
    { x: 700, y: 250 },
],
[
    { x: 100, y: 400},
    { x: 200, y: 300 + 100 },
    { x: 300, y: 380 + 50 },
    { x: 400, y: 420 + 100 },
    { x: 500, y: 220 + 250 },
    { x: 600, y: 115 + 30 },
    { x: 700, y: 250 + 20 },
]
]
const points2: Point[][] = [[
    { x: 0, y: 350 },
    { x: 700, y: 250 },
], [
    { x: 0, y: 250 },
    { x: 700, y: 350 },
]]

const LinePage = (props: Props) => {

    const [lineCount, LineRange] = useRange(30, 1, 30, "Line Count");
    const [lineSplineSteps, SplineRange] = useRange(13, 1, 30, "Spline Step Size");
    const [smooth, SmoothRange] = useRange(2, 0, 8, "Chaikin smooth iterations");
    const [simplfy, SimplifyRange] = useRange(0, 0, 100, "Simplify Cooficiant");

    return (
        <>
            <LineCanvas size={{
                x: 1200,
                y: 1200,
            }}
                lines={points}
                lineCount={lineCount}
                lineSplineSteps={lineSplineSteps}
                smooth={smooth}
                simplfy={simplfy}
            />
            <Row>
                <Col span={8}>
                    <Range {...LineRange} />
                </Col>
                <Col span={8}>
                    <Range {...SplineRange} />
                </Col>
                <Col span={8}>
                    <Range {...SmoothRange} />
                </Col>
                <Col span={8}>
                    <Range {...SimplifyRange} />
                </Col>
            </Row>
        </>
    )
}

export default LinePage